import React from 'react'
import Box from '../styles/Box.js'
import Text from '../styles/Text.js'

const CategoryList = () => (
  <div>
    <Text p={1} fontSize={2} fontWeight={600} letterSpacing={2} color='lightGreen' bg='darkGreen' textAlign='center'></Text>
    <Box my='-.5px' p={1} border='1px solid' borderColor='lightGreen'>
      <Text fontSize={2} fontWeight={600} letterSpacing={2} color='darkGreen' bg='white' textAlign='center'></Text>
    </Box>
    <Box my='-.5px' p={1} border='1px solid' borderColor='lightGreen'>
      <Text fontSize={2} fontWeight={600} letterSpacing={2} color='darkGreen' bg='white' textAlign='center'></Text>
    </Box>
    <Box my='-.5px' p={1} border='1px solid' borderColor='lightGreen'>
      <Text fontSize={2} fontWeight={600} letterSpacing={2} color='darkGreen' bg='white' textAlign='center'></Text>
    </Box>
    <Box my='-.5px' p={1} border='1px solid' borderColor='lightGreen'>
      <Text fontSize={2} fontWeight={600} letterSpacing={2} color='darkGreen' bg='white' textAlign='center'></Text>
    </Box>
  </div>
)

export default CategoryList



